@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";
// @import "~bootstrap/scss/bootstrap";

    * {
        font-family: 'Roboto' !important;
        font-weight: 400;
        font-style: normal;
        color: $text-main-color;
        text-decoration: none;

        min-height: 0;
        min-width: 0;
    }
    body {
        height: 100%;
        // display: flex;
        flex-direction: column;
    }
    main {
        flex: 1 0 auto;
        overflow: hidden;
    }


    .container {
        margin: 0 auto;
        padding: 0 340px;
        box-sizing: border-box;
    }
    .breadcrumbs {
        margin: 40px 0;
        .container {
            display: flex;
            justify-content: start;
            align-items: center;
        }
        .breadcrumbs__link {
            font-weight: 400;
            font-size: 14px;
            line-height: 24px;
            /* identical to box height, or 171% */
            color: #00529C; 
            text-decoration: none; 
            &:not(:first-child) {
                margin-left: 3px;
            }
            &:not(:last-child)::after {
                content: ' / ';
                font-weight: 400;
                font-size: 14px;
                line-height: 24px;
                /* identical to box height, or 171% */
                color: #00529C;
            }
        }
    }
    .btn {
        @include btn();
    }
    .btn-transparent {
        @include btn-transparent();
    }
    .page-header {
        @include page-header();
    }
    .page__slider-controll {
        @include slider-btn();
    }
    .fancybox-bg {
        background: rgba(217, 229, 240, 0.8);       
    }

    .sitemap {
        padding: 80px 0 80px;
        box-sizing: border-box;
        background-color: #00529C;
        min-height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        .container {

        }
        .sitemap__list {

        }
        .sitemap__list__item {
            border: 3px solid #fff;
            border-radius: 20px;
            font-weight: 400;
            font-size: 20px;
            line-height: 24px; 
            min-width: 250px; 
            overflow: hidden;
            
            &:not(:last-child) {
                margin-bottom: 25px;
            }
            a {
                display: flex;
                justify-content: center;
                align-items: center;
                text-align: center;
                padding: 10px 20px;
                box-sizing: border-box;
                min-height: 70px;
                color: #fff;

                &:hover {
                    background-color: #fff;
                    color: #00529C;
                }
            }
        }
    }

    .header {
        background: #fff;
        padding: 17px 0 18px;
        box-sizing: border-box;

        .container {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        .header__wrap {
            display: flex;
            justify-content: start;
            align-items: center;
        }
        .header__logo {
            margin-right: 37px;
            min-width: 61px;
            height: 64px;
        }
        .header__logo-desc {
            font-weight: 500;
            font-size: 20px;
            line-height: 104.9%;
            /* or 34px */
            color: #00529C;
            max-width: 240px;
        }
        .header__phone-btn {
            margin-right: 30px;
        }
        .header__order-btn {

        }
        .burger-box {
            cursor: pointer;
            width: 54px;
            min-width: 54px;
            display: flex;
            justify-content: start;
            flex-direction: column;
            margin-left: 30px;
            &::before {
                content: '';
                background: #00529C;
                height: 6px;
                border-radius: 10px;
                width: 100%;   
                margin-bottom: 6px;             
            }
            &::after {
                content: '';
                background: #00529C;
                height: 6px;
                border-radius: 10px;
                width: 100%;   
                margin-top: 6px;             
            }            
        }
        .burger__item {
            background: #00529C;
            height: 6px;
            border-radius: 10px;
            width: 100%;
        }

        .header__overlay {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1000;

            height: 100vh;
            width: 100vw;
            box-sizing: border-box;
            padding-right: 340px;
            background: rgba(217, 229, 240, 0.8);

            display: none;
        }
        .header__nav {
            margin-left: auto;
            width: max-content;
            display: flex;
            justify-content: start;
            align-items: flex-start;
        }
        .header__close {
            border: none;
            outline: none;
            background: transparent;
            width: 28px;
            height: 28px;
            position: relative;
            margin-top: 46px;
            margin-right: 19px;
            cursor: pointer;

            &::before, &::after {
                content: '';
                height: 28px;
                width: 2px;
                background: #00529C;
                display: block;

                position: absolute;
                top: 0;
                left: 50%;

            }
            &::before {
                transform: rotate(45deg);
            }
            &::after {
                transform: rotate(-45deg);
            }
        }
        .header__nav__list {
            max-width: 280px;
            min-width: 280px;
            width: 100%;
            padding: 40px 0 21px;
            background: #00529C;
            border-radius: 0px 0px 28px 28px; 
            box-sizing: border-box;   
            
            a {
                display: block;
            }
            .btn-transparent {
                display: none;
                border-color: #fff;
                color: #fff;
                margin-right: 0;
                font-weight: 700;
                font-size: 18px;
                line-height: 20px; 
                max-width: 200px;
                min-width: 200px; 
                margin: 40px auto 20px;              
            }
            .btn {
                display: none;
                font-weight: 700;
                font-size: 18px;
                line-height: 20px;
                margin: 0 auto; 
                max-width: 200px;
                min-width: 200px;                 
            }
        }
        .header__nav__list__item {
            box-sizing: border-box;
            a {
                padding: 19px 41px;
                font-weight: 400;
                font-size: 18px;
                line-height: 20px;
                color: rgba(255, 255, 255, 0.5);
                text-align: right;
                display: block;
            }
            &:hover {
                background: $main-color;
                a {
                    color: #fff;
                }             
            }
        }
    }
    
    .title {
        position: relative;
        .title__slider__controlls {
            position: absolute;
            right: 340px;
            bottom: 42px;

            display: flex;
            justify-content: start;
            align-items: center;
