    .advantages {
        padding-bottom: 80px;
        .container {
        }
        .advantages__headline {
            margin-bottom: 95px;
        }
        .advantages__wrapper {
            position: relative;
        }
        .advantages__controlls {
            display: flex;
            justify-content: start;
            align-items: center;
            position: absolute;
            top: -90px;
            right: 0;

        }
        .advantages__controll {
            &:first-child {
                margin-right: 20px;
            }
            &:last-child {
                transform: scale(-1);
            }
        }
        .advantages__slider {
            border: 2px solid #00529C;
            border-radius: 20px;
            flex: 0 0 auto;
            height: auto;
            .slick-list {
                overflow: visible;
            }
            
        }
        .slick-track {
            display: flex;
        }
        .slick-track .slick-active .advantages__slide__overlay {
            border-radius: 20px 20px 0 20px;
            left: -3px;
        }
        .slick-track .slick-active + .slick-active .advantages__slide__overlay {
            border-radius: 20px 20px 0 0;
            left: 0px;
        }
        .slick-track .slick-active + .slick-active + .slick-active + .slick-active .advantages__slide__overlay {
            border-radius: 20px 20px 0 0;
            left: 3px;
        }
        .slick-track .slick-active + .slick-active + .slick-active + .slick-active + .slick-active .advantages__slide__overlay {
            border-radius: 20px 20px 20px 0;
            left: 3px;
        }
        .advantages__slide {
            height: auto;
            flex: 0 0 auto;
            padding: 40px 21px 25px;
            box-sizing: border-box;
            min-height: 250px;
            position: relative;
            opacity: 0;
            transition: all .3s;
            &.slick-active {
                opacity: 1;
            }
            &.slick-active:nth-child(1) .advantages__slide__overlay {
                border-radius: 20px 20px 0 20px;
            }   
                    

            &:hover {
                .advantages__slide__overlay {
                    opacity: 1;
                }
            }
        }
        .advantages__slide__overlay {
            pointer-events: none;
            opacity: 0;
            transition: all .3s;
            position: absolute;
            bottom: -3px;
            left: 0;
            width: 100%;
            padding: 52px 21px 60px;
            border-radius: 20px 20px 0px 0px;
            background: $main-color;
            box-sizing: border-box;
            min-height: 303px;

            .advantages__slide__icon {
                background: #00529C;                   
            }
            .advantages__slide__desc {
                color: #fff;
            }
        }
        .advantages__slide__icon {
            background: #FF7A00;
            border-radius: 20px;   
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 auto; 
            margin-bottom: 25px;
            width: 80px;
            height: 80px;        
            img {

            }
        }
        .advantages__slide__desc {
            display: block;
            max-width: 283px;
            width: 100%;
            margin: 0 auto;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            text-align: center;
            
            color: #00529C;            
        }
    }
