//Тут храняться миксины

@mixin test() {
    background-color: red;
}

@mixin btn() {
    cursor: pointer;
    background: $main-color;
    border-radius: 100px; 
    padding: 13px 0;
    min-width: 240px;
    max-width: 240px;
    box-sizing: border-box; 
    
    display: block;

    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    
    text-align: center;
    text-transform: uppercase;
    
    color: #FFFFFF;
    
    @media screen and (max-width: 680px) {
        max-width: 200px !important;
        min-width: 200px;

        font-weight: 700;
        font-size: 14px;
        line-height: 18px;        
    }
}
@mixin btn-transparent() {
    cursor: pointer;
    border: 2px solid #00529C;
    border-radius: 100px; 
    min-width: 240px; 

    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    
    /* or 160% */
    text-align: center;
    text-transform: uppercase;
    white-space: nowrap;
    padding: 10px 0;
    color: #1552AC; 
    box-sizing: border-box;
    max-width: 240px;   
}
@mixin slider-btn() {
    width: 45px;
    height: 45px;
    background: $main-color;
    border-radius: 50%;
    border: none;
    outline: none;
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    img {
        width: 11px;
        height: 16px;
    }
    @media screen and (max-width: 680px) { 
        width: 45px;
        height: 45px;
    }
}

@mixin page-header() {
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 47px;
    margin-bottom: 40px;
    
    color: #00529C;   
    
    @media screen and (max-width: 680px) {
        font-weight: 700;
        font-size: 24px;
        line-height: 28px;
        margin-bottom: 26px;        
    }
}